.product{
    display:flex;
    flex-direction: column;
    background-color: white;
    z-index: 1;
    align-items: center;
    justify-content: flex-end;
    margin: 10px;
    padding: 10px;
    width: 100%;
    max-height: 400px;
    min-width: 100px;
}
/* .product_image{
    height: 300px;
    width: 300px;
} */
.product_info{
    height: 100px;
    margin-bottom: 15px;
    width: 100%;
}
.product_price{
    margin-top: 5px;
}
.product > img{
    max-height: 250px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 15px;
}
.product>button{
    background: #17a2b8;
    border: 2px solid;
    margin-top: 10px;
    border-color: darkcyan;
    color: whitesmoke;
}
@media screen and (max-width:768px) and (orientation:portrait){
    .addto{
        font-size: small;
        
    }
    .product{
        margin: 5px;
    }
    .product > img{
        max-height: 250px;
        width: 100%;
        object-fit: contain;
        margin-bottom: 15px;
    }
    .product_info{
        font-size: small;
    }
}