.checkoutProduct{
    display: flex;
    margin-top: 20px;
    margin-bottom: 20px;
}
.checkoutProduct_info{
    padding-left: 20px;
}
.checkoutProduct_info>button{
    background-color: teal;
    border: 1px solid;
    margin-top: 10px;
    border-color: thistle;
    color: white;
}
.checkoutProduct_image{
    object-fit: contain;
    width: 180px;
    height: 180px;
}
.checkoutProduct_title{
    font-size: 17px;
    font-weight: 800;
}

@media screen and (max-width: 768px) and (orientation: portrait) {
    .checkoutProduct_info>button{
        font-size: small;
    }   
    .checkoutProduct_image{
        object-fit: contain;
        width: 120px;
        height: 120px;
    }
    .checkoutProduct_title{
        font-size: 14px;
        font-weight: 600;
    }
}