.login_container{
    align-items: center;
    justify-content: center;
}

.modal-content{
    align-items: center;
    justify-content: center;

}
.login_button{
    background: #17a2b8;
    border: 2px solid;
    margin-top: 10px;
    border-color: darkcyan;
    color: whitesmoke;
}
.input{
    width: 350px;
    background-color: #dddddd;
}
#hide1{
    display:none;
}
.input{
    font-size: 22px;
}
@media screen and (max-width:768px) and (orientation:portrait){
    .input{
        max-width: 270px;
        font-size: 22px !important;
    }
}