  justify-content: center;
  align-items: center;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.navbar {
  background-color: #fff;
  display: flex;
  flex: 1 1;
  align-items: center !important;
  box-shadow: 0px 2px 7px 03px teal;
  position: relative;
}
#fix {
  position: fixed;
  z-index: 2;
  top: 0;
}
.navbar-brand {
  font-size: 1.9rem !important;
  color: darkcyan !important;
  /* padding: 20px; */
  padding: 0;
  padding-left: 20px;
  margin-left: 20px;
}
.navbar a {
  font-size: 1.5rem;
  text-transform: capitalize;
  margin-right: 30px;
}
.navbar a:hover {
  color: darkcyan !important;
}
.menu_active {
  font-weight: bold;
  /* border-bottom: 2px solid black; */
}
.nav-link {
  font-family: "Poppins", sans-serif;
}
#header {
  width: 100%;
  height: 80vh;
  margin-top: 30px;
}
#header h1 {
  margin: 0 0 10px 0;
  font-size: 48px;
  font-weight: 700;
  color: black;
}
#header .brand-name {
  color: darkcyan;
}
#header h2 {
  color: black;
  font-size: 24px;
}
.goto {
  background: #17a2b8;
  border: 2px solid;
  margin-top: 10px;
  border-color: darkcyan;
  color: whitesmoke;
}
#header .btn-get-started {
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 10px 30px;
  border-radius: 50px;
  transition: 0.5s;
  color: darkcyan;
  border: 2px solid darkgray;
  text-decoration: none;
}
#header .btn-get-started:hover {
  background: darkcyan;
  color: #fff;
}
#header-img {
  text-align: right;
}
#header .animated {
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
}
.basket {
  display: flex;
  align-items: center;
  color: teal;
  margin-top: 10px;
}
.basket_count {
  margin-top: 10px;
  margin-right: 15px;
  font-size: x-large;
}
@-webkit-keyframes up-down {
  0% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(-10px);
  }
}
@keyframes up-down {
  0% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(-10px);
  }
}
.mood {
  margin-top: -7px;
}
.card {
  display: inline;
    
}
.card-body{
  background-color: #fff;
}
.Gap {
  height: 40px;
}
.home-msg {
  font-size: 40px;
  font-family: 'Itim', cursive;
}
.basketMobile{
  display: none;
  
}
#mssg1{
  font-size: x-large;
  font-weight: 500;
}
.sanitizer{
  height: 300px;
  width: 300px;
}

@media screen and (max-width: 768px) and (orientation: portrait) {
  * {
    padding-top: 3px;
  }
  .card-body{
     
  }
  .ad1{
    margin: 0 auto;
  }
  .card-img-top{
    height: 200px;
    width: 200px;
  }
  .sanitizer{
    height: 200px;
    width: 200px;
  }
  .basketMobile{
    margin-top: 400px;
    display: block;
    position: fixed;
    z-index: 5;
    right: 0px;
    background: #a3c1c5;
    border-radius: 50%;
    border: 1px dashed;
    border-color: black;
    height: 65px;
    width: 65px;
  }
  .collapse {
    padding: 15px;
  }
  #fix {
    background-color: rgb(234, 237, 237);
    position: fixed;
    z-index: 2;
    top: 0;
  }
  .navbar {
    background-color: #fff;
    display: flex;
    flex: 1 1;
    align-items: center !important;
    padding: 7px;
  }
  .navbar-brand {
    font-size: 1rem !important;
    color: darkcyan !important;
    /* padding: 15px; */
    padding-left: 15px;
    margin-left: 3px;
  }
  .navbar a {
    font-size: 1.5rem;
    text-transform: capitalize;
    /* margin-right: 30px; */
  }
  .navbar-toggler {
    margin-right: 10px;
  }
  .img-fluid {
    margin-top: 50px;
  }
  .home-msg {
    font-size: xx-large;
  }
  .emailguest{
    font-size: x-large;
  }
  #mssg1{
    font-size: large;
    font-weight: 500;
  }
}

